<template>
  <div id="qrcode">
    <h1>https://github.com/gerardreches/vue-qrcode-component</h1>
    <qr-code :text="text"></qr-code>
    {{text}}
  </div>
</template>
<script>
import qrCode from 'vue-qrcode-component';

export default {
  name: 'occupy',
  data() {
    return {
      text: 'Init Data',
    };
  },
  methods: {},
  components: {
    qrCode,
  },
  mounted() {
    setTimeout(() => {
      this.text = 'Change Data In 10s';
    }, 10000);
  },
};
</script>
<style lang="scss" scoped>
#qrcode {
  height: 100vh;
  background: red;
}
</style>



